import Vue from "vue";
import App from "./App.vue";
import VueRouter from "vue-router";
import Routs from "./routs";

Vue.use(VueRouter);
Vue.config.productionTip = false;

const router = new VueRouter({
  routes: Routs,
  mode: "history",
});

new Vue({
  render: (h) => h(App),
  router: router,
}).$mount("#app");
